  <div id="useguide-wrapper">
    <h1># 创建 vue vue-router element-ui axios 项目</h1>

    <div class="bg-lightgray-block">
    <pre>
      npm install -g cnpm --registry=https://registry.npm.taobao.org
      cnpm install -g vue-cli

      vue init webpack whn-personal-admin
      cd whn-personal-admin

      cnpm install
      cnpm install vue-router -S
      cnpm install axios -S
      cnpm i element-ui -S

      npm run dev
      npm run build
    </pre>
    </div>

    <h1>Layout 布局</h1>
    <p>通过基础的 24 分栏，迅速简便地创建布局。</p>

    <p>
      基础布局:
      使用单一分栏创建基础的栅格布局。通过 row 和 col 组件，并通过 col 组件的 span 属性我们就可以自由地组合布局。
    </p>

    <div>
      <el-row>
        <el-col :span="24">
          <div class="grid-content bg-purple-dark"></div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <div class="grid-content bg-purple"></div>
        </el-col>
        <el-col :span="12">
